<template>
    <div>
        <h1>Subscription</h1>

        <h3>Status</h3>
        <ul>
            <li>Registered: {{ registered }}</li>
            <li>Subscribed: {{ subscribed }}</li>
            <li>Status: {{ subscriptionStatus }}</li>
        </ul>

        <h3>Actions</h3>
        <button v-if="registered" @click="toggleSubscription">{{ subscribed ? 'Unsubscribe' : 'Subscribe' }}</button>
        <p v-else>Service worker is not registered</p>

        <h3>Subscription</h3>
        <p v-if="subscription !== null" class="payload">{{ JSON.stringify(subscription) }}</p>
    </div>
</template>

<script>
import { mapState } from 'vuex'
export default {
    computed: {
        ...mapState({
            registered: state => state.worker.registration !== null,
            subscribed: state => state.worker.subscription !== null,
            subscription: state => state.worker.subscription,
            subscriptionStatus: state => state.worker.subscriptionStatus
        })
    }, 
    methods : {
        toggleSubscription () {
            console.log(this.subscription)
            if (this.subscribed) {
                this.$store.dispatch('worker/unsubscribe')
                .then(subscription => { console.log(subscription) })
                .catch(error => { console.log(error) })
            }
            else {
                this.$store.dispatch('worker/subscribe')
                .then(subscription => { console.log(subscription) })
                .catch(error => { console.log(error) })
            }
        }
    }
}
</script>

<style scoped>
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
.payload {
    max-width: 70vw;
    overflow-wrap: break-word;
    text-align: center;
    margin-left: auto;
    margin-right: auto;
}
</style>
